import { useRef, useEffect, useState, type ReactNode, type CSSProperties } from "react";
import { Renderer, Program, Triangle, Mesh } from "ogl";
import React from "react";

/* ============================
   CONFIG (EDIT THESE)
============================ */

// Background color behind rays
const BG_COLOR = [0.04, 0.05, 0.08]; // dark blue-gray

/* ============================
   Utils
============================ */
const hexToRgb = (hex: string): number[] => {
  const m = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
  return m
    ? [
      parseInt(m[1], 16) / 255,
      parseInt(m[2], 16) / 255,
      parseInt(m[3], 16) / 255,
    ]
    : [1, 1, 1];
};

const getAnchorAndDir = (origin: string, w: number, h: number): { anchor: number[], dir: number[] } => {
  const outside = 0.2;
  switch (origin) {
    case "top-left":
      return { anchor: [0, -outside * h], dir: [0, 1] };
    case "top-right":
      return { anchor: [w, -outside * h], dir: [0, 1] };
    case "left":
      return { anchor: [-outside * w, h * 0.5], dir: [1, 0] };
    case "right":
      return { anchor: [(1 + outside) * w, h * 0.5], dir: [-1, 0] };
    case "bottom-left":
      return { anchor: [0, (1 + outside) * h], dir: [0, -1] };
    case "bottom-center":
      return { anchor: [w * 0.5, (1 + outside) * h], dir: [0, -1] };
    case "bottom-right":
      return { anchor: [w, (1 + outside) * h], dir: [0, -1] };
    default:
      return { anchor: [w * 0.5, -outside * h], dir: [0, 1] };
  }
};

/* ============================
   Type Definitions
============================ */
interface LightRaysBGProps {
  children?: ReactNode;
  raysOrigin?: string;
  raysColor?: string;
  raysSpeed?: number;
  lightSpread?: number;
  rayLength?: number;
  pulsating?: boolean;
  fadeDistance?: number;
  saturation?: number;
  followMouse?: boolean;
  mouseInfluence?: number;
  noiseAmount?: number;
  distortion?: number;
  style?: CSSProperties;
}

/* ============================
   Component
============================ */
export default function LightRaysBG({
  children,
  raysOrigin = "top-center",
  raysColor = "#0d00ffff",
  raysSpeed = 1.5,
  lightSpread = 0.8,
  rayLength = 1.2,
  pulsating = false,
  fadeDistance = 1.0,
  saturation = 1.0,
  followMouse = true,
  mouseInfluence = 0.1,
  noiseAmount = 0.1,
  distortion = 0.05,
  style = {},
}: LightRaysBGProps) {
  const bgRef = useRef<HTMLDivElement>(null);
  const rafRef = useRef<number | null>(null);
  const mouseRef = useRef({ x: 0.5, y: 0.5 });
  const smoothMouseRef = useRef({ x: 0.5, y: 0.5 });
  const [visible, setVisible] = useState(false);

  /* ============================
     Visibility
  ============================ */
  useEffect(() => {
    if (!bgRef.current) return;
    const io = new IntersectionObserver(
      ([e]) => setVisible(e.isIntersecting),
      { threshold: 0.1 }
    );
    io.observe(bgRef.current);
    return () => io.disconnect();
  }, []);

  /* ============================
     WebGL
  ============================ */
  useEffect(() => {
    if (!visible || !bgRef.current) return;

    //  FIX #1: alpha FALSE (opaque canvas)
    const renderer = new Renderer({
      dpr: Math.min(window.devicePixelRatio, 2),
      alpha: false,
    });

    const gl = renderer.gl;

    //  FIX #2: set background color
    gl.clearColor(BG_COLOR[0], BG_COLOR[1], BG_COLOR[2], 1);

    gl.canvas.style.width = "100%";
    gl.canvas.style.height = "100%";
    bgRef.current.innerHTML = "";
    bgRef.current.appendChild(gl.canvas);

    const vert = `
attribute vec2 position;
varying vec2 vUv;
void main(){
  vUv = position * 0.5 + 0.5;
  gl_Position = vec4(position,0.,1.);
}`;

    const frag = `precision highp float;
uniform float iTime;
uniform vec2 iResolution;
uniform vec2 rayPos;
uniform vec2 rayDir;
uniform vec3 raysColor;
uniform float raysSpeed;
uniform float lightSpread;
uniform float rayLength;
uniform float pulsating;
uniform float fadeDistance;
uniform float saturation;
uniform vec2 mousePos;
uniform float mouseInfluence;
uniform float noiseAmount;
uniform float distortion;
varying vec2 vUv;

float noise(vec2 p){
  return fract(sin(dot(p,vec2(12.9898,78.233)))*43758.5453);
}

float ray(vec2 src, vec2 dir, vec2 p, float a, float b){
  vec2 d = p - src;
  vec2 n = normalize(d);
  float ca = dot(n,dir);
  float da = ca + distortion * sin(iTime*2.0+length(d)*0.01)*0.2;
  float spread = pow(max(da,0.0),1.0/max(lightSpread,0.001));
  float dist = length(d);
  float maxD = iResolution.x * rayLength;
  float lf = clamp((maxD-dist)/maxD,0.,1.);
  float ff = clamp((iResolution.x*fadeDistance-dist)/(iResolution.x*fadeDistance),0.5,1.);
  float pulse = pulsating>0.5 ? (0.8+0.2*sin(iTime*raysSpeed*3.)) : 1.;
  float base = clamp(
    (0.45+0.15*sin(da*a+iTime*raysSpeed))+
    (0.3+0.2*cos(-da*b+iTime*raysSpeed)),0.,1.);
  return base*lf*ff*spread*pulse;
}

void main(){
  vec2 coord = vec2(gl_FragCoord.x,iResolution.y-gl_FragCoord.y);
  vec2 d = rayDir;
  if(mouseInfluence>0.){
    d = normalize(mix(rayDir, normalize(mousePos*iResolution-rayPos), mouseInfluence));
  }
  float r = ray(rayPos,d,coord,36.2,21.1)*0.6
          + ray(rayPos,d,coord,22.4,18.0)*0.4;
  vec3 col = r * raysColor;
  if(noiseAmount>0.) col *= mix(1.,noise(coord*0.01+iTime*0.1),noiseAmount);
  float b = 1.-coord.y/iResolution.y;
  col *= vec3(0.2+b*0.8,0.4+b*0.6,0.6+b*0.4);
  float g = dot(col,vec3(0.299,0.587,0.114));
  col = mix(vec3(g),col,saturation);
  gl_FragColor = vec4(col,1.);
}`;

    const uniforms = {
      iTime: { value: 0 },
      iResolution: { value: [1, 1] },
      rayPos: { value: [0, 0] },
      rayDir: { value: [0, 1] },
      raysColor: { value: hexToRgb(raysColor) },
      raysSpeed: { value: raysSpeed },
      lightSpread: { value: lightSpread },
      rayLength: { value: rayLength },
      pulsating: { value: pulsating ? 1 : 0 },
      fadeDistance: { value: fadeDistance },
      saturation: { value: saturation },
      mousePos: { value: [0.5, 0.5] },
      mouseInfluence: { value: mouseInfluence },
      noiseAmount: { value: noiseAmount },
      distortion: { value: distortion },
    };

    const mesh = new Mesh(gl, {
      geometry: new Triangle(gl),
      program: new Program(gl, { vertex: vert, fragment: frag, uniforms }),
    });

    const resize = () => {
      if (!bgRef.current) return;
      const w = bgRef.current.clientWidth;
      const h = bgRef.current.clientHeight;
      renderer.setSize(w, h);
      uniforms.iResolution.value = [w * renderer.dpr, h * renderer.dpr];
      const { anchor, dir } = getAnchorAndDir(
        raysOrigin,
        w * renderer.dpr,
        h * renderer.dpr
      );
      uniforms.rayPos.value = anchor;
      uniforms.rayDir.value = dir;
    };

    const loop = (t: number) => {
      //  FIX #3: clear each frame
      gl.clear(gl.COLOR_BUFFER_BIT);

      uniforms.iTime.value = t * 0.001;
      smoothMouseRef.current.x =
        smoothMouseRef.current.x * 0.9 + mouseRef.current.x * 0.1;
      smoothMouseRef.current.y =
        smoothMouseRef.current.y * 0.9 + mouseRef.current.y * 0.1;
      uniforms.mousePos.value = [
        smoothMouseRef.current.x,
        smoothMouseRef.current.y,
      ];
      renderer.render({ scene: mesh });
      rafRef.current = requestAnimationFrame(loop);
    };

    resize();
    window.addEventListener("resize", resize);
    rafRef.current = requestAnimationFrame(loop);

    return () => {
      if (rafRef.current) cancelAnimationFrame(rafRef.current);
      window.removeEventListener("resize", resize);
      renderer.gl.getExtension("WEBGL_lose_context")?.loseContext();
      renderer.gl.canvas.remove();
    };
  }, [visible]);

  /* ============================
     Mouse
  ============================ */
  useEffect(() => {
    if (!followMouse) return;
    const move = (e: MouseEvent) => {
      if (!bgRef.current) return;
      const r = bgRef.current.getBoundingClientRect();
      mouseRef.current = {
        x: (e.clientX - r.left) / r.width,
        y: (e.clientY - r.top) / r.height,
      };
    };
    window.addEventListener("mousemove", move);
    return () => window.removeEventListener("mousemove", move);
  }, [followMouse]);

  return (
    <div style={{ position: "relative", ...style }}>
      {/* Background boundary (defines 100vh render area) */}
      <div
        style={{
          position: "relative",
          height: "100vh",
          overflow: "hidden",
          zIndex: 0,
        }}
      >
        <div
          ref={bgRef}
          style={{
            position: "absolute",
            inset: 0,
            pointerEvents: "none",
          }}
        />
      </div>

      {/* Content pulled back over the background */}
      <div
        style={{
          position: "relative",
          zIndex: 1,
          marginTop: "-100vh",
        }}
      >
        {children}
      </div>
    </div>
  );

}
