import React, { useEffect, useMemo, useState } from "react";
import { Menu, X, Sparkles, LogIn, UserPlus, Globe, LogOut } from "lucide-react";
import { useLanguage } from "../i18n/LanguageContext";

/**
 * Universal location hook:
 * - Works in Gatsby + Vite
 * - No react-router dependency
 * - SSR-safe
 */
function useUniversalLocation() {
  const [loc, setLoc] = useState(() => {
    if (typeof window === "undefined") {
      return { pathname: "/", search: "", hash: "" };
    }
    return {
      pathname: window.location.pathname || "/",
      search: window.location.search || "",
      hash: window.location.hash || "",
    };
  });

  useEffect(() => {
    if (typeof window === "undefined") return;

    const read = () => {
      setLoc({
        pathname: window.location.pathname || "/",
        search: window.location.search || "",
        hash: window.location.hash || "",
      });
    };

    read();

    window.addEventListener("popstate", read);
    window.addEventListener("hashchange", read);

    return () => {
      window.removeEventListener("popstate", read);
      window.removeEventListener("hashchange", read);
    };
  }, []);

  return loc;
}

/**
 * Universal Link:
 * - Gatsby: uses <Link> from gatsby (client routing)
 * - Vite/other: uses normal <a>
 *
 * NOTE: dynamic import avoids Gatsby/Vite build issues
 */
function SmartLink(props: React.PropsWithChildren<any>) {
  const { to, href, onClick, className, children, ...rest } = props;
  const target = to || href || "#";

  const [GatsbyLink, setGatsbyLink] = useState<any>(null);

  useEffect(() => {
    if (typeof window === "undefined") return;

    (async () => {
      try {
        const gatsbyModuleName = "gatsby";
        // eslint-disable-next-line no-eval
        const gatsby = await eval(`import(${JSON.stringify(gatsbyModuleName)})`);
        if (gatsby?.Link) {
          setGatsbyLink(() => gatsby.Link);
        }
      } catch {
        // not Gatsby
      }
    })();
  }, []);

  if (GatsbyLink) {
    return (
      <GatsbyLink to={target} onClick={onClick} className={className} {...rest}>
        {children}
      </GatsbyLink>
    );
  }

  return (
    <a href={target} onClick={onClick} className={className} {...rest}>
      {children}
    </a>
  );
}

export const Header = () => {
  const [isVisible, setIsVisible] = useState(true);
  const [lastScrollY, setLastScrollY] = useState(0);
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [isAuthenticated, setIsAuthenticated] = useState(false);

  const location = useUniversalLocation();
  const { language, setLanguage, t } = useLanguage();

  // Check authentication status on mount
  useEffect(() => {
    if (typeof window === "undefined") return;
    const token = localStorage.getItem("authToken");
    setIsAuthenticated(!!token);
  }, []);

  // Close menu on route change
  useEffect(() => {
    setIsMenuOpen(false);
  }, [location.pathname, location.search, location.hash]);

  useEffect(() => {
    if (typeof window === "undefined") return;

    const handleScroll = () => {
      const currentScrollY = window.scrollY;

      if (currentScrollY < 10 || isMenuOpen) {
        setIsVisible(true);
      } else if (currentScrollY > lastScrollY) {
        setIsVisible(false);
      } else {
        setIsVisible(true);
      }

      setLastScrollY(currentScrollY);
    };

    window.addEventListener("scroll", handleScroll, { passive: true });
    return () => window.removeEventListener("scroll", handleScroll);
  }, [lastScrollY, isMenuOpen]);

  const scrollToSection = (e: React.MouseEvent<HTMLAnchorElement>, id: string) => {
    if (location.pathname !== "/") {
      return;
    }

    e.preventDefault();

    const element = document.getElementById(id);
    if (element) {
      element.scrollIntoView({ behavior: "smooth" });
      setIsMenuOpen(false);
    }
  };

  const toggleLanguage = () => {
    setLanguage(language === "en" ? "vn" : "en");
  };

  return (
    <>
      <nav
        className={`glass-header ${isVisible ? "visible" : "hidden"} ${
          isMenuOpen ? "menu-open" : ""
        }`}
      >
        <div className="header-inner">
          <SmartLink to="/" className="header-logo" onClick={() => setIsMenuOpen(false)}>
            <div className="logo-dot"></div>
            <span>A.ID</span>
          </SmartLink>

          {/* Desktop Navigation */}
          <div className="header-nav desktop-only">
            <SmartLink to="/" onClick={(e: any) => scrollToSection(e, "home")}>
              {t("common.home")}
            </SmartLink>
            <SmartLink to="/" onClick={(e: any) => scrollToSection(e, "process")}>
              {t("common.process")}
            </SmartLink>
            <SmartLink to="/" onClick={(e: any) => scrollToSection(e, "examples")}>
              {t("common.examples")}
            </SmartLink>
            <SmartLink to="/#buy">{t("common.pricing")}</SmartLink>

            <div className="nav-divider"></div>

            {/* Language Toggle */}
            <button className="lang-toggle" onClick={toggleLanguage} aria-label="Toggle Language">
              <Globe size={14} />
              <span>{language.toUpperCase()}</span>
            </button>

            <div className="nav-divider"></div>

            {isAuthenticated ? (
              <SmartLink to="/logout" className="header-cta logout-btn">
                <LogOut size={14} />
                <span>Logout</span>
              </SmartLink>
            ) : (
              <>
                <SmartLink to="/login" className="login-link">
                  {t("common.login")}
                </SmartLink>
                <SmartLink to="/signup" className="header-cta">
                  <span>{t("common.getStarted")}</span>
                  <Sparkles size={14} />
                </SmartLink>
              </>
            )}
          </div>

          {/* Mobile Menu Toggle */}
          <button
            className="mobile-toggle"
            onClick={() => setIsMenuOpen(!isMenuOpen)}
            aria-label="Toggle Menu"
          >
            {isMenuOpen ? <X size={24} /> : <Menu size={24} />}
          </button>
        </div>

        {/* Mobile Dropdown Menu */}
        <div className={`mobile-menu ${isMenuOpen ? "open" : ""}`}>
          <div className="mobile-menu-content">
            <div className="mobile-nav-links">
              <SmartLink to="/" onClick={(e: any) => scrollToSection(e, "home")}>
                <span>01.</span> {t("common.home")}
              </SmartLink>
              <SmartLink to="/" onClick={(e: any) => scrollToSection(e, "process")}>
                <span>02.</span> {t("common.process")}
              </SmartLink>
              <SmartLink to="/" onClick={(e: any) => scrollToSection(e, "examples")}>
                <span>03.</span> {t("common.examples")}
              </SmartLink>
              <SmartLink to="/buy">
                <span>05.</span> {t("common.pricing")}
              </SmartLink>
            </div>

            {/* Mobile Language Toggle */}
            <div className="mobile-lang-switch">
              <button
                className={`lang-btn ${language === "en" ? "active" : ""}`}
                onClick={() => setLanguage("en")}
              >
                EN
              </button>
              <div className="lang-divider"></div>
              <button
                className={`lang-btn ${language === "vn" ? "active" : ""}`}
                onClick={() => setLanguage("vn")}
              >
                VN
              </button>
            </div>

            <div className="mobile-auth-actions">
              {isAuthenticated ? (
                <SmartLink to="/logout" className="mobile-auth-btn logout">
                  <LogOut size={18} />
                  Logout
                </SmartLink>
              ) : (
                <>
                  <SmartLink to="/login" className="mobile-auth-btn login">
                    <LogIn size={18} />
                    {t("common.login")}
                  </SmartLink>
                  <SmartLink to="/signup" className="mobile-auth-btn signup">
                    <UserPlus size={18} />
                    {t("common.getStarted")}
                    <Sparkles size={14} className="sparkle-icon" />
                  </SmartLink>
                </>
              )}
            </div>
          </div>
        </div>
      </nav>

      {/* Backdrop for mobile menu */}
      {isMenuOpen && <div className="menu-backdrop" onClick={() => setIsMenuOpen(false)}></div>}
    </>
  );
};
