import { useEffect, useRef } from "react";
import * as THREE from "three";
import { TrackballControls } from "three/examples/jsm/controls/TrackballControls";
import {
  CSS3DRenderer,
  CSS3DSprite,
} from "three/examples/jsm/renderers/CSS3DRenderer";
import React from 'react';
// @ts-ignore
import spriteUrl from "./sprite.png";

const COUNT = 512;
const SPHERE_RADIUS = 750;
const CUBE_SIZE = 900;

const EXPLODE_MULTIPLIER = 2.0;
const EXPLODE_RANDOMNESS = 0.9;

const MORPH_DELAY = 3000;
const MORPH_DURATION = 1000;
const HOLD_TIME = 7000;

type Shape = "sphere" | "cube" | "explode";

type Particle = {
  sprite: CSS3DSprite;
  sphere: THREE.Vector3;
  cube: THREE.Vector3;
  explode: THREE.Vector3;
  phase: number;
};

export default function Css3DSprites({
  interactionRef,
}: {
  interactionRef: React.RefObject<HTMLDivElement | null>;
}) {
  const containerRef = useRef<HTMLDivElement | null>(null);

  useEffect(() => {
    const container = containerRef.current;
    const interaction = interactionRef.current;
    if (!container || !interaction) return;

    const scene = new THREE.Scene();

    const camera = new THREE.PerspectiveCamera(75, 1, 1, 8000);
    camera.position.set(600, 400, 1500);

    const renderer = new CSS3DRenderer();
    renderer.domElement.style.position = "absolute";
    renderer.domElement.style.inset = "0";
    renderer.domElement.style.pointerEvents = "none"; // visuals only
    container.appendChild(renderer.domElement);

    //  CONTROLS BOUND TO INTERACTION ZONE
    const controls = new TrackballControls(camera, interaction);
    controls.rotateSpeed = 1.6;
    controls.zoomSpeed = 1.2;
    controls.panSpeed = 0.8;

    const resize = () => {
      const w = container.clientWidth;
      const h = container.clientHeight;
      camera.aspect = w / h;
      camera.updateProjectionMatrix();
      renderer.setSize(w, h);
    };
    resize();
    const ro = new ResizeObserver(resize);
    ro.observe(container);

    const particles: Particle[] = [];
    const img = new Image();
    img.src = spriteUrl;

    let morphStart = 0;
    let holding = false;

    const shapes: Shape[] = ["explode", "sphere", "cube", "explode"];
    let shapeIndex = 0;
    let fromShape: Shape = "explode";
    let toShape: Shape = "sphere";

    img.onload = () => {
      const perSide = Math.round(Math.cbrt(COUNT));
      const step = CUBE_SIZE / (perSide - 1);
      const half = CUBE_SIZE / 2;

      for (let i = 0; i < COUNT; i++) {
        const u = Math.random();
        const v = Math.random();
        const theta = 2 * Math.PI * u;
        const phi = Math.acos(2 * v - 1);

        const sx = SPHERE_RADIUS * Math.sin(phi) * Math.cos(theta);
        const sy = SPHERE_RADIUS * Math.sin(phi) * Math.sin(theta);
        const sz = SPHERE_RADIUS * Math.cos(phi);

        const xi = i % perSide;
        const yi = Math.floor(i / perSide) % perSide;
        const zi = Math.floor(i / (perSide * perSide));

        const cx = xi * step - half;
        const cy = yi * step - half;
        const cz = zi * step - half;

        const cubeVec = new THREE.Vector3(cx, cy, cz);

        const randomOffset = new THREE.Vector3(
          (Math.random() * 2 - 1) * CUBE_SIZE * EXPLODE_RANDOMNESS,
          (Math.random() * 2 - 1) * CUBE_SIZE * EXPLODE_RANDOMNESS,
          (Math.random() * 2 - 1) * CUBE_SIZE * EXPLODE_RANDOMNESS
        );

        const explodeVec = cubeVec
          .clone()
          .multiplyScalar(EXPLODE_MULTIPLIER)
          .add(randomOffset);

        const sprite = new CSS3DSprite(
          img.cloneNode(true) as HTMLImageElement
        );
        sprite.position.copy(explodeVec);
        scene.add(sprite);

        particles.push({
          sprite,
          sphere: new THREE.Vector3(sx, sy, sz),
          cube: cubeVec,
          explode: explodeVec,
          phase: Math.random() * Math.PI * 2,
        });
      }

      setTimeout(() => {
        morphStart = performance.now();
      }, MORPH_DELAY);
    };

    let rafId = 0;
    const animate = () => {
      rafId = requestAnimationFrame(animate);

      const now = performance.now();
      const t = now * 0.001;

      let morph = 0;
      if (morphStart > 0) {
        morph = (now - morphStart) / MORPH_DURATION;

        if (morph >= 1 && !holding) {
          morph = 1;
          holding = true;

          setTimeout(() => {
            shapeIndex = (shapeIndex + 1) % shapes.length;
            fromShape = shapes[shapeIndex];
            toShape = shapes[(shapeIndex + 1) % shapes.length];
            morphStart = performance.now();
            holding = false;
          }, HOLD_TIME);
        }

        morph = Math.min(1, Math.max(0, morph));
        morph = morph * morph * (3 - 2 * morph);
      }

      for (const p of particles) {
        const a = p[fromShape];
        const b = p[toShape];

        const x = THREE.MathUtils.lerp(a.x, b.x, morph);
        const y = THREE.MathUtils.lerp(a.y, b.y, morph);
        const z = THREE.MathUtils.lerp(a.z, b.z, morph);

        const base = new THREE.Vector3(x, y, z);
        const dir =
          base.lengthSq() === 0
            ? new THREE.Vector3(1, 0, 0)
            : base.clone().normalize();

        const wobble = Math.sin(t + p.phase) * 4;
        p.sprite.position.copy(base.add(dir.multiplyScalar(wobble)));

        const s = 1 + Math.sin(t * 1.5 + p.phase) * 0.25;
        p.sprite.scale.set(s, s, s);
      }

      controls.update();
      renderer.render(scene, camera);
    };

    animate();

    return () => {
      cancelAnimationFrame(rafId);
      ro.disconnect();
      controls.dispose();
      renderer.domElement.remove();
      particles.length = 0;
    };
  }, [interactionRef]);

  return <div ref={containerRef} style={{ position: "absolute", inset: 0 }} />;
}
